.header {
  background-image: url(../images/photos/header__background.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;
    height: 100vh;
    padding: 20px 20px 80px;
  }

  &__menu {
    display: flex;
    align-items: center;
    justify-content: center;

  }

  &__menu-opener {
    display: block;
    height: 12px;
    width: 18px;
    background-image: url(../images/menu.svg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }

  &__menu-opener:hover {
    background-image: url(../images/menu_hover.svg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

  }

  &__call {
    height: 18px;
    width: 18px;
    background-image: url(../images/phone.svg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin-right: 30px;
    text-decoration: none;

    &::before {
      content: "+1 234 555-55-55";
      display: block;
      position: relative;
      top: 50%;
      transform: translate(-100%, -50%);
      color: #fff;
      width: max-content;
      padding-right: 12px;
      pointer-events: none;
      opacity: 0;
      transition: opacity 0.3s;
    }
    &:hover::before {
      pointer-events: all;
      opacity: 1;
    }

  }

  &__title {
    font-size: 36px;
    line-height: 36px;
    color: #fff;
    text-align: center;
  }
}

.logo {

  &__image {
    height: 38px;
    width: 130px;
  }
}
